<template>
    <div class="login-wrap">
        <div class="login-content">
            <div class="login-title">{{pageType[this.paramsType]['title']}}</div>
            <van-field label-align="right" label-width="80" v-model="tel" type="tel" label="手机号" />
            <van-field label-align="right" label-width="80" v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
                <template #button>
                    <van-button size="small" type="primary" :disabled="isDisabled" @click="sendCode">
                        <span v-if="!isDisabled">{{buttonTxt}}</span>
                        <van-count-down ref="countDown" v-show="isDisabled" :time="time" format="ss" #default="timeData" @finish="finish" :auto-start="false">
                            <span style="color: white">{{ timeData.seconds }}</span>
                            <span style="color: white">s重新获取</span>
                        </van-count-down>
                    </van-button>
                </template>
            </van-field>
            <div style="padding: 20vw 5vw 5vw 5vw"><van-button type="info" block @click="goIndex">登录</van-button></div>
        </div>
        <div class="tips-block" v-if="this.paramsType == 'manager'">
            <p>提示：物管手机需要提前在公租房管理系统中设置</p>
        </div>
        <div class="tips-block" v-if="this.paramsType == 'user'">
            <p>提示：</p>
            <p>1、首次登录或未完善个人信息的，登录进入【我的】，提示完善资料</p>
            <p>2、已完成资料的直接进入【首页】</p>
        </div>
    </div>
</template>
<script>
import { Field, Button, CountDown  } from 'vant';
export default {
    components: {
        [Field.name]: Field,
        [Button.name]: Button,
        [CountDown.name]: CountDown
    },
    data() {
        return {
            paramsType: 'manager',
            pageType: {
                manager: {
                    title: '物业管理',
                    documentTitle: '物管登录',
                },
                user: {
                    title: '用户登录',
                    documentTitle: '用户登录',
                }
            },
            tips: '',
            tel: '',
            sms: '',
            isDisabled: false,
            buttonTxt: '发送验证码',
            time: 60 * 1000
        }
    },
    created() {
        this.paramsType = this.$route.params.type;
        document.title = this.pageType[this.paramsType].documentTitle
        localStorage['login_type'] = this.paramsType;
    },
    methods: {
        sendCode() {
            this.isDisabled = true,
            this.$refs.countDown.start();
            this.$axios.post(`/h5/common/sms/${this.tel}`).then(res => {
                console.log(res)
            })
        },
        finish() {
            this.isDisabled = false;
        },
        goIndex() {
            let url = '';
            let path = '';
            if(this.paramsType == 'user') {
                url = '/h5/user/login/normal';
                path = '/home/house';
            } else {
                url = '/h5/user/login/manage';
                path = '/repair/pending'
            }
            this.$axios.post(url, {
                mobile: this.tel,
                smsCode: this.sms
            }).then(res => {
                debugger
                console.log(res)
                localStorage['token'] = res.data;
                this.$router.push(path)
            })
        }
    }
}
</script>
<style lang="less" scoped>
.login-wrap {
    position:absolute;
    width: 100vw;
    height: 100vh;
    .login-title {
        text-align: center;
        height: 25vh;
        line-height: 25vh;
        font-size: 22px;
    }
    .login-content {
        width: 100%;
        background: white;
    }
    .tips-block {
        color: #999;
        padding: 0 5vw;
        margin-top: 10vw;
        p {
            margin: 2vw;
            font-size: 14px;
        }
    }
}
</style>
